import React from 'react';
import ProfileMenuItem from './ProfileMenuItem';
import {components} from './data';

class Profile extends React.Component {
    temp = (e, c) => {
        this.menuItems.map((c)=>{
            c.changeActiveStatus(false);
            return c;
        });
        c.changeActiveStatus(true);
        this.changeSectionContent(c.props.content);
    }

    state = {
        sectionContent: components['education'].content
    }

    changeSectionContent = (content)=>{
        this.setState({
            sectionContent: content
        });
    }

    constructor(props) {
        super (props);
        this.menuItems = [];
        console.log(this.menuItems);
    }
 
    render() {
        return (
            <div id="profile" className="animated fadeIn">
                <menu className="profile-menu">
                    {Object.keys(components).map((key) => {
                        let data = components[key];
                        return <ProfileMenuItem
                                    key={key} 
                                    name= {data.name} 
                                    active={data.isActive} 
                                    clickHandler={this.temp} 
                                    parent={this}
                                    content={data.content}/>;
                    })}
                </menu>
                <section id="profile-section">
                    {React.createElement(this.state.sectionContent, {}, null)}
                </section>
            </div>
        );
    }
}

export default Profile;