import React from 'react';
import { Link } from 'react-router-dom';

class ProfileMenuItem extends React.Component {
    state = {
        isActive: this.props.active
    }

    /**
     * constructor
     * @param {*} props 
     */
    constructor(props) {
        super (props);
        this.parent = this.props.parent;
        this.parent.menuItems.push(this);
        this.classes = ["profile-menu-item", "animated", "fadeIn"];
    }

    /**
     * function to handle onclick event
     * @param {event} e 
     */
    onClick = (e) => {
        this.props.clickHandler(e, this);
    }

    /**
     * function to handle onmouseover event
     * @param {event} e 
     */
    onMouseOver = (e) => {
        e.target.style.cursor="pointer";
        e.target.classList.add("active")
    }

    /**
     * function to handle onmouseout event
     * @param {event} e 
     */
    onMouseOut = (e) => {
        if (this.state.isActive === false)
            e.target.classList.remove("active");
    }

    /**
     * fucntion to update active status of current components
     * @param {active status} s 
     */
    changeActiveStatus = (s) => {
        this.setState((state) => {
            return ({isActive: s});
        });
    }

    /**
     * function to apply current states
     */
    apply = () => {
        if (this.state.isActive && this.classes.indexOf("active") === -1) {
            this.classes.push("active");
        } else if (!this.state.isActive) {
            this.classes = this.classes.filter((c) => {
                return c !=='active';
            });
        }
    }

    render() {
        this.apply();
        return (
            <div 
                className={this.classes.join(" ")}
                onClick={this.onClick} 
                onMouseOver={this.onMouseOver} 
                onMouseOut={this.onMouseOut}>
                    {this.props.name}
            </div>
        );
    }
}

export default ProfileMenuItem;